import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../components/Main.vue'
import Nation from '../components/Nation.vue'
import Province from '../components/Province.vue'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main,
    },
    {
      path: '/nation',
      name: 'Nation',
      component: Nation,
    },
    {
      path: '/province',
      name: 'Province',
      component: Province,
    },
  ],
})

export default router
