<template>
  <div id="Province">
    <div id="title">{{ this.$route.query.provinceName }}地区疫情情况</div>
    <el-divider></el-divider>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-card id="map-card" shadow="never">
          <div id="provincePie"></div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card id="table-card" shadow="never">
          <el-table
            :data="provinceTable"
            style="width: 100%;"
            border
            id="province-table"
            :cell-style="{ 'vertical-align': 'top' }"
          >
            <el-table-column prop="name" label="统计类型"></el-table-column>
            <el-table-column prop="value" label="统计人数"></el-table-column>
          </el-table>
        </el-card>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <div id="provinceBar"></div>
    <el-divider></el-divider>
    <el-timeline>
      <el-timeline-item
        v-for="(item, i) in timelineTitle"
        :key="i"
        :timestamp="timeStamp[i]"
        placement="top"
      >
        <el-card>
          <div
            v-for="(news, index) in timelineText[i]"
            :key="index"
            class="news"
          >
            {{ news }}
          </div>
        </el-card>
      </el-timeline-item>
    </el-timeline>
  </div>
</template>

<script>
import echarts from 'echarts'

export default {
  name: 'Province',
  data() {
    return {
      timeStamp: this.$store.state.mainModule.timeStamp,
      timelineTitle: this.$store.state.mainModule.timeStamp,
      timelineText: this.$store.state.mainModule.timelineText,
      provinceTable: [],
      provinceBar: '',
      provincePie: '',
      currentConfirmedCount: [],
      curedCount: [],
      deadCount: [],
      emphasisStyle: {
        itemStyle: {
          barBorderWidth: 1,
          shadowBlur: 10,
          shadowOffsetX: 0,
          shadowOffsetY: 0,
          shadowColor: 'rgba(0,0,0,0.5)',
        },
      },
      pieOption: {
        title: {
          text: '各市区累计确诊数',
          left: 'center',
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)',
        },

        series: [
          {
            name: '确诊人数',
            type: 'pie',
            radius: '55%',
            center: ['50%', '50%'],
            data: [],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)',
              },
            },
          },
        ],
      },
      barOption: {
        backgroundColor: '#eee',
        legend: {
          data: ['现存确诊', '累计治愈人数', '累计死亡'],
          left: 10,
        },
        brush: {
          toolbox: ['rect', 'polygon', 'lineX', 'lineY', 'keep', 'clear'],
          xAxisIndex: 0,
        },
        toolbox: {
          feature: {
            magicType: {
              type: ['stack,tiled'],
            },
            dataView: {},
          },
        },
        tooltip: {},
        xAxis: {
          data: [],
          name: '市区名称',
          axisLine: { onZero: true },
          splitLine: { show: false },
          splitArea: { show: false },
          axisLabel: {
            interval: 0,
          },
        },
        yAxis: {
          inverse: false,
          splitArea: { show: false },
        },
        grid: {
          left: 100,
        },
        visualMap: {
          type: 'continuous',
          dimension: 1,
          text: ['Max', 'min'],
          inverse: false,
          itemHeight: 200,
          calculable: true,
          min: 0,
          max: 500,
          top: 60,
          left: 10,
          inRange: {
            colorLightness: [0.8, 0.4],
          },
          outOfRange: {
            color: '#bbb',
          },
          controller: {
            inRange: {
              color: '#2f4554',
            },
          },
        },
        series: [
          {
            name: '现存确诊',
            type: 'bar',
            stack: 'one',
            emphasis: this.emphasisStyle,
            data: [],
          },
          {
            name: '累计治愈人数',
            type: 'bar',
            stack: 'one',
            emphasis: this.emphasisStyle,
            data: [],
          },
          {
            name: '累计死亡',
            type: 'bar',
            stack: 'one',
            emphasis: this.emphasisStyle,
            data: [],
          },
        ],
      },
    }
  },
  watch: {
    '$store.state.mainModule.shouldProvinceDataShown': function() {
      if (this.$store.state.mainModule.shouldProvinceDataShown == true) {
        this.drawProvinceBar('provinceBar')
        this.drawProvinceTable()
        this.drawProvincePie('provincePie')
        this.$store.commit('mainModule/resetFlag')
      }
    },
    '$route.query.provinceName': function() {
      this.$store.dispatch(
        'mainModule/getInstantProvinceData',
        this.$route.query.provinceName
      )
      this.$store.dispatch(
        'mainModule/getProvincePolicy',
        this.$route.query.provinceName
      )
    },
    '$store.state.mainModule.timelineText': function() {
      this.timeStamp = this.$store.state.mainModule.timeStamp
      this.timelineTitle = this.$store.state.mainModule.timeStamp
      this.timelineText = this.$store.state.mainModule.timelineText
    },
  },
  methods: {
    test() {
      console.log(this.timeStamp)
    },
    drawProvinceBar(id) {
      console.log('draw map')
      for (
        var i = 0;
        i < this.$store.state.mainModule.provinceData.cities.length;
        i++
      ) {
        this.barOption.xAxis.data.push(
          this.$store.state.mainModule.provinceData.cities[i].cityName
        )
        this.barOption.series[0].data.push(
          this.$store.state.mainModule.provinceData.cities[i]
            .currentConfirmedCount
        )
        this.barOption.series[1].data.push(
          this.$store.state.mainModule.provinceData.cities[i].curedCount
        )
        this.barOption.series[2].data.push(
          this.$store.state.mainModule.provinceData.cities[i].deadCount
        )
      }
      this.provinceBar = echarts.init(document.getElementById(id))
      this.provinceBar.setOption(this.barOption)
      //console.log('finish', id, this.mapOption)
    },
    drawProvinceTable() {
      this.provinceTable = [
        {
          name: '现存确诊病例',
          value: this.$store.state.mainModule.provinceData
            .currentConfirmedCount,
        },
        {
          name: '累计确诊病例',
          value: this.$store.state.mainModule.provinceData.confirmedCount,
        },
        {
          name: '累计疑似病例',
          value: this.$store.state.mainModule.provinceData.suspectedCount,
        },
        {
          name: '累计治愈病例',
          value: this.$store.state.mainModule.provinceData.curedCount,
        },
        {
          name: '累计死亡病例',
          value: this.$store.state.mainModule.provinceData.deadCount,
        },
      ]
      //console.log(this.$store.state.mainModule.provinceData)
    },
    drawProvincePie(id) {
      console.log('draw pie')
      this.pieOption.series[0].data = []
      for (
        var i = 0;
        i < this.$store.state.mainModule.provinceData.cities.length;
        i++
      ) {
        this.pieOption.series[0].data.push({
          value: this.$store.state.mainModule.provinceData.cities[i]
            .confirmedCount,
          name: this.$store.state.mainModule.provinceData.cities[i].cityName,
        })
      }
      this.pieOption.series[0].data.sort(function(a, b) {
        return a.value - b.value
      })
      this.provincePie = echarts.init(document.getElementById(id))
      this.provincePie.setOption(this.pieOption)
      //console.log(this.pieOption)
    },
  },
  created() {
    console.log('create Province')
    this.$store.dispatch(
      'mainModule/getInstantProvinceData',
      this.$route.query.provinceName
    )
    this.$store.dispatch(
      'mainModule/getProvincePolicy',
      this.$route.query.provinceName
    )
  },
}
</script>

<style>
#title {
  font-size: 60px;
  margin: 2% 0 0 2%;
  text-align: left;
}
#map-card {
  margin: 10px 0 0 0;
  width: 100%;
  height: 600px;
}
#table-card {
  margin: 10px 0 0 0;
  width: 100%;
  height: 600px;
}
#province-table {
  margin: 155px 0 0 0;
}
#provinceBar {
  margin: 2% 0 0 0;
  width: 100%;
  height: 800px;
}
#provincePie {
  width: 100%;
  height: 550px;
}
.el-timeline {
  margin: 2% 0 0 0;
}
.news {
  text-align: left;
}
</style>
