const mutations = {
  getInstantNationData(state, payload) {
    state.nationData = []
    state.chinaIndex = 0
    state.chinaData = []
    //console.log(payload)
    var tempDead = []
    var tempConfirmed = []
    for (var i = 0; i < payload.length; i++) {
      if (payload[i].countryName == '中国') {
        if (payload[i].provinceShortName == '中国') {
          state.chinaIndex = i
          state.chinaData = payload[i]
          continue
        }
        state.nationData.push({
          provinceShortName: payload[i].provinceShortName,
          currentConfirmedCount: payload[i].currentConfirmedCount,
          confirmedCount: payload[i].confirmedCount,
        })
        state.confirmedLine.push(payload[i].confirmedCount)
        state.curedLine.push(payload[i].curedCount / payload[i].confirmedCount)
        state.deadLine.push(payload[i].deadCount / payload[i].confirmedCount)
        state.xAxis.push(payload[i].provinceShortName)

        if (payload[i].deadCount == 0) {
          tempDead.push(payload[i].provinceShortName)
        }
        var len = tempConfirmed.length
        if (len == 0) {
          tempConfirmed.push({
            name: payload[i].provinceShortName,
            value: payload[i].confirmedCount,
          })
        } else if (len < 3) {
          tempConfirmed.push({
            name: payload[i].provinceShortName,
            value: payload[i].confirmedCount,
          })
          tempConfirmed.sort((a, b) => {
            return b.value - a.value
          })
        } else if (tempConfirmed[len - 1].value < payload[i].confirmedCount) {
          tempConfirmed.push({
            name: payload[i].provinceShortName,
            value: payload[i].confirmedCount,
          })
          tempConfirmed.sort((a, b) => {
            return b.value - a.value
          })
          tempConfirmed.pop()
        }
      }
    }
    //console.log(tempConfirmed)
    var nationCuredRate =
      state.chinaData.curedCount / state.chinaData.confirmedCount
    var nationDeadRate =
      state.chinaData.deadCount / state.chinaData.confirmedCount
    state.carouselData.push([
      '截止目前，',
      '全国治愈率为' + nationCuredRate + '，',
      '死亡率为' + nationDeadRate + '。',
    ])
    state.carouselData.push([
      '截至目前',
      '全国疫情最严重的省份及地区分别是' +
        tempConfirmed[0].name +
        ',' +
        tempConfirmed[1].name +
        ',' +
        tempConfirmed[2].name +
        ',',
      '累计确诊人数为' +
        tempConfirmed[0].value +
        ',' +
        tempConfirmed[1].value +
        ',' +
        tempConfirmed[2].value +
        '.',
    ])
    state.carouselData.push([
      '截至目前，',
      '全国共有' + tempDead.length + '个省份及地区的死亡率为零：',
      tempDead.join(','),
    ])

    state.shouldNationDataShown = true
    //console.log(state.carouselData)
  },
  getInstantProvinceData(state, payload) {
    state.provinceData = []
    //console.log(payload)
    var city = []
    if (payload.cities.length > 0) {
      for (var i = 0; i < payload.cities.length; i++) {
        city.push({
          cityName: payload.cities[i].cityName,
          currentConfirmedCount: payload.cities[i].currentConfirmedCount,
          confirmedCount: payload.cities[i].confirmedCount,
          suspectedCount: payload.cities[i].suspectedCount,
          curedCount: payload.cities[i].curedCount,
          deadCount: payload.cities[i].deadCount,
        })
      }
    } else {
      city.push({
        cityName: payload.provinceShortName,
        currentConfirmedCount: payload.currentConfirmedCount,
        confirmedCount: payload.confirmedCount,
        suspectedCount: payload.suspectedCount,
        curedCount: payload.curedCount,
        deadCount: payload.deadCount,
      })
    }
    state.provinceData = {
      provinceShortName: payload.provinceShortName,
      currentConfirmedCount: payload.currentConfirmedCount,
      confirmedCount: payload.confirmedCount,
      suspectedCount: payload.suspectedCount,
      curedCount: payload.curedCount,
      deadCount: payload.deadCount,
      cities: city,
    }
    state.shouldProvinceDataShown = true
    //console.log(state.provinceData)
  },
  getProvincePolicy(state, data) {
    //console.log(data)
    state.timeStamp = []
    state.timelineText = []
    var i = 0
    for (; i < data.length; i += 7) {
      var j = i
      var startDate = new Date(data[j].date)
      var startDM = startDate.getMonth() + 1 + '月' + startDate.getDate() + '日'
      var temp = []
      //console.log(startDM, data[j].date)
      for (; j < i + 7 && j < data.length; j++) {
        if (data[j].policy.length > 1) {
          var note = '等' + data[j].policy.length + '条'
          var curDate = new Date(data[j].date)
          var curDM = curDate.getMonth() + 1 + '月' + curDate.getDate() + '日：'
          temp.push(curDM + data[j].policy[0].title + ' ' + note)
        }
      }
      var endDate = new Date(data[j - 1].date)
      var endDM = endDate.getMonth() + 1 + '月' + endDate.getDate() + '日'
      //console.log(endDM, data[j - 1].date)
      if (temp.length > 0) {
        state.timelineText.push(temp)
        state.timeStamp.push(startDM + '-' + endDM)
      }
    }
    console.log(state.timelineText)
  },
  resetFlag(state) {
    if (state.shouldNationDataShown == true) {
      state.shouldNationDataShown = false
    }
    if (state.shouldProvinceDataShown == true) {
      state.shouldProvinceDataShown = false
    }
  },
}

const actions = {
  getInstantNationData(context) {
    var param = '/area?latest=1'
    Axios.get(state.urlPref + param)
      .then(function(res) {
        context.commit('getInstantNationData', res.data.results)
        //console.log(res)
      })
      .catch(function(err) {
        console.log('error in getNationData. err message', err)
      })
  },
  getInstantProvinceData(context, provinceName) {
    //console.log(provinceName)
    var param =
      '/area?latest=1&provinceEng=' +
      state.provinceNameEng[state.provinceName.indexOf(provinceName)]
    Axios.get(state.urlPref + param)
      .then(function(res) {
        context.commit('getInstantProvinceData', res.data.results[0])
        //console.log(res)
      })
      .catch(function(err) {
        console.log('error in getProvinceData. err message', err)
      })
  },
  getProvincePolicy(context, provinceName) {
    var date = new Date()
    var month = date.getMonth()
    var day = date.getDate()
    var param =
      '/vi/api/policy?provinceName=' +
      provinceName +
      '&startMonth=0&startDate=24&endMonth=' +
      month +
      '&endDate=' +
      day
    Axios.get(param)
      .then(function(res) {
        context.commit('getProvincePolicy', res.data.data)
        //console.log(res)
      })
      .catch(function(err) {
        console.log('error in getPolicy. err message', err)
      })
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
}
